<!--
 * @Descripttion: z
 * @version: z
 * @Author: ZJM
 * @Date: 2022-08-15 10:25:18
 * @LastEditors: ZJM
 * @LastEditTime: 2022-12-13 15:20:15
-->
<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{ kanbanConfig.title }}</p>
      <p id="time">{{ time }}</p>
      <div :class="showLogo()"></div>
    </div>
    <div class="kanbanContent">
      <div class="device_wrapper">
        <card-module
          title='车间状态'
          cardBg='cardBg_large'
          titleBg='cardTitle_small'
          height='100%'
        >
          <div class="workshop_status">
            <div :gutter="10" class="device_count">
              <div
                :span="3" class="item_count"
                :style="{ color: item.color }"
                v-for="item in deviceCount"
                :key="item.name"
              >
                <span class="name">{{ item.name }}</span>
                <span class="value">{{ item.value }}</span>
              </div>
            </div>
            <div class="device_content">
              <div class="device_content_item" v-for="item in deviceList" :key="item.deviceCode" >
                <div class="device_state">
                  <span class="name">{{ item.deviceName }}</span>
                  <div class="state">
                    <div :class="['outer', { overTime: item.overtime } ]" v-for="state in item.lightStates" :key="state">
                        <span v-if="item.deviceRealState !== 3" class="item_state" :class="state">{{ stateEnum[state] }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </card-module>
      </div>
      <div class="right_content">
        <card-module
          title='当前设备状态'
          height='32%'
        >
          <div class="warn_list">
            <div class="warn_content">
              <table cellpadding="0" cellspacing="0" class="tableHead">
                <tr>
                  <td style="width: 6%"></td>
                  <td style="width: 14%">设备名称</td>
                  <td style="width: 10%">类型</td>
                  <td style="width: 12%">呼叫时间</td>
                  <td style="width: 12%">响应时间</td>
                  <td style="width: 10%">维修时长</td>
                  <td style="width: 15%">维修人员</td>
                  <td style="width: 20%">状态</td>
                </tr>
              </table>
              <vue-seamless-scroll :data="warnList" :class-option="optionHover" class="seamless-warp" ref="seamlessTable">
                <table id="warnTable" cellpadding="0" cellspacing="0">
                  <tr v-for="(item, index) in warnList" :key="index">
                    <td style="width: 6%" class="lamp"><div :class="{ warnRed: item.type === '异常', yellowLight: item.type === '维修' }"></div></td>
                    <td style="width: 14%" class="active">{{item.deviceName}}</td>
                    <td style="width: 10%" class="active">{{item.type}}</td>
                    <td style="width: 12%" class="active">{{item.requestTime}}</td>
                    <td style="width: 12%" class="active">{{item.responseTime || '-'}}</td>
                    <td style="width: 10%" class="active">{{item.responseDuration ? item.responseDuration.toFixed(0) + '分钟' : '-'}}</td>
                    <td style="width: 15%" class="active">{{ item.repairPerson || '-' }}</td>
                    <td style="width: 20%" class="active">{{item.state}}</td>
                  </tr>
                </table>
              </vue-seamless-scroll>
            </div>
          </div>
        </card-module>
        <card-module
          title='当日设备呼叫明细'
          height='32%'
        >
          <div class="warn_list">
            <div class="warn_content">
              <table cellpadding="0" cellspacing="0" class="tableHead">
                <tr>
                  <td style="width: 15%">设备名称</td>
                  <td style="width: 30%">类型</td>
                  <td style="width: 25%">累计时长</td>
                  <td style="width: 25%">呼叫次数</td>
                </tr>
              </table>
              <vue-seamless-scroll :data="calloutList" :class-option="optionHover" class="seamless-warp" ref="seamlessTable">
                <table id="warnTable" cellpadding="0" cellspacing="0">
                  <tr v-for="(item, index) in calloutList" :key="index">
                    <td style="width: 15%" class="active">{{item.deviceName}}</td>
                    <td style="width: 30%" :class="['active', fontStateEnum[item.type]]">{{item.type}}</td>
                    <td style="width: 25%" class="active">{{item.time ? item.time.toFixed(0) + '分钟' : '-'}}</td>
                    <td style="width: 25%" class="active">{{item.count}}</td>
                  </tr>
                </table>
              </vue-seamless-scroll>
            </div>
          </div>
        </card-module>
        <div style="height: 36%" class="charts_wra">

          <card-module
            title='当日呼叫次数分析'
            width='60%'
            height='100%'
            titleWidth='80%'
          >
            <div class="warn_chart">
              <div style="width: 100%; height: 100%" ref="calloutChart"></div>
            </div>
          </card-module>
          <card-module
            title='当月维修时长分析'
            titleBg="cardTitle_lesser"
            width="39%"
            height='100%'
            titleWidth='95%'
          >
            <div style="width: 100%; height: 100%" ref="maintainChart"></div>
          </card-module>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
// import vueSeamlessScroll from 'vue-seamless-scroll';
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import { getAndonKanbanOfRiSheng, listDeviceExceptionCount } from '@/api/kanban';
import { getAndonExceptionWarnReport, listDeviceCallDetail, listRepairTimeAnalysis } from '@/api/andon';
export default {
  name: 'kanban',
  components: {
    // vueSeamlessScroll,
    CardModule
  },
  data() {
    return {
      title: '',
      groupId: 0,
      time: '',
      dataTime: '',
      kanbanConfig: { title: '安灯看板', key: '', time: 15000 },
      stateEnum: {
        yellow_off: '停用',
        red_e: '异常',
        blue_m: '保养',
        green_w: '待料',
        white_m: '维护',
        green_p: '生产'
      },
      fontStateEnum: {
        停用: 'yellow_off_font',
        异常: 'red_e_font',
        保养: 'blue_m_font',
        待料: 'green_w_font',
        维护: 'white_m_font',
        生产: 'green_p_font'
      },
      deviceCount: [
        {
          name: '全部',
          value: 0,
          color: '#4bf3f9',
          key: ''
        },
        {
          name: '生产',
          value: 0,
          color: '#00e62c',
          key: 'green_p'
        },
        {
          name: '停用',
          value: 0,
          color: '#FFD307',
          key: 'yellow_off'
        },
        {
          name: '维护',
          value: 0,
          color: '#FFFFFF',
          key: 'white_m'
        },
        {
          name: '保养',
          value: 0,
          color: '#07aaff',
          key: 'blue_m'
        },
        {
          name: '异常',
          value: 0,
          color: '#FC4A45',
          key: 'red_e'
        },
        {
          name: '待料',
          value: 0,
          color: '#7e7bff',
          key: 'green_w'
        }
      ],
      warnList: [],
      calloutList: [],
      deviceList: [],
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      timer2: null
    };
  },
  computed: {
    optionHover() {
      return {
        step: 0.4, // 数值越大速度滚动越快
        limitMoveNum: 5, // 开始无缝滚动的数据量 this.list.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000 // 单步运动停止的时间(默认值1000ms)
      };
    }
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
    this.loadData();
    this.getExceptionCount();
    this.timer = setInterval(() => {
      this.loadData();
    }, this.kanbanConfig.time || 3000);
    this.timer2 = setInterval(() => {
      this.getExceptionCount();
    }, 8000);
  },
  created() {
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.getDataTime();
  },
  destroyed() {
    this.timer && setInterval(this.timer);
    this.timer2 && setInterval(this.timer2);
  },
  methods: {
    getDataTime() {
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    fontSize(num = 10) {
      const clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    // 绘制呼叫次数柱状图
    drawCalloutChart(xData, seriesData) {
      const barChart = this.$echarts.init(this.$refs.calloutChart);
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '17%',
          left: '3%',
          right: '8%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: xData,
          axisTick: {
            alignWithLabel: true
          },
          axisLine: {
            lineStyle: {
              color: '#929EE6'
            }
          },
          axisLabel: {
            fontSize: 10,
            color: '#D8D8D8'
          },
          nameTextStyle: {
            fontSize: 10
          },
          name: '设备',
          nameGap: '5'
        }],
        yAxis: [{
          type: 'value',
          name: '次数',
          minInterval: 1,
          axisLine: {
            lineStyle: {
              color: '#929EE6'
            }
          },
          axisLabel: {
            fontSize: 10,
            color: '#D8D8D8'
          },
          nameTextStyle: {
            fontSize: 10
          },
          splitLine: {
            show: false
          }
        }],
        series: [
          {
            type: 'bar',
            barWidth: '35%',
            itemStyle: {
              color: '#09DBA0'
            },
            label: {
              show: true,
              position: 'top',
              color: 'auto'
            },
            data: seriesData
          }
        ]
      };
      barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        barChart.resize();
      });
    },
    // 绘制当月维修时长分析
    drawMaintainChart(XData, chartsData) {
      const maintainChart = this.$echarts.init(this.$refs.maintainChart);
      const option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
          // formatter: (params) => {
          //   return `${params.marker}`
          // }
        },
        grid: {
          top: '10%',
          left: '8%',
          right: '18%',
          bottom: '5%',
          containLabel: true
        },
        yAxis: {
          axisLine: {
            lineStyle: {
              color: '#929EE6'
            }
          },
          axisLabel: {
            fontSize: 10,
            color: '#D8D8D8'
          },
          nameTextStyle: {
            fontSize: 10
          },
          data: XData
        },
        xAxis: {
          name: '分钟',
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#929EE6'
            }
          },
          axisLabel: {
            fontSize: 10,
            color: '#D8D8D8'
          },
          nameTextStyle: {
            fontSize: 10
          }
        },
        series: [{
          name: '维修时长',
          type: 'bar',
          barWidth: '40%',
          itemStyle: {
            color: '#FAE93A'
          },
          label: {
            show: true,
            position: 'right',
            color: '#FFFFFF'
          },
          data: chartsData
        }]
      };
      maintainChart.setOption(option);
    },
    // 获取安灯异常次数
    async getExceptionCount() {
      const formData = new FormData();
      formData.append('key', this.kanbanConfig.key || 'oOg1hjGXEDw=');
      formData.append('current', this.barChartInfo.current);
      formData.append('size', this.barChartInfo.size);
      try {
        const res = await listDeviceExceptionCount({}, formData, {
          headers: {
            'Content-Type': 'form-data'
          }
        });
        const { rows = [], total } = res;
        const data = rows.map(item => item.exceptionCount);
        const xData = rows.map(item => item.deviceName);
        this.drawCalloutChart(xData, data);
        if (this.barChartInfo.current * this.barChartInfo.size >= total) {
          this.barChartInfo.current = 1;
        } else {
          this.barChartInfo.current++;
        }
      } catch (error) {}
    },
    // 获取当日设备呼叫明细
    async getTodayCallout() {
      try {
        const res = await listDeviceCallDetail({
          current: 1,
          size: 9999,
          key: this.kanbanConfig.key || 'oOg1hjGXEDw='
        }, {});
        this.calloutList = res.rows || [];
      } catch (error) {}
    },
    // 获取当月维修时长分析
    async getMonthMaintain() {
      try {
        const res = await listRepairTimeAnalysis({
          current: this.maintainAnalyse.current,
          size: this.maintainAnalyse.size,
          key: this.kanbanConfig.key || 'oOg1hjGXEDw='
        }, {});
        const { rows = [], total } = res;
        const chartsData = rows.map(item => item.time.toFixed(0));
        const XData = rows.map(item => item.userName);
        this.drawMaintainChart(XData, chartsData);
        if (this.maintainAnalyse.current * this.maintainAnalyse.size >= total) {
          this.maintainAnalyse.current = 1;
        } else {
          this.maintainAnalyse.current++;
        }
      } catch (error) {}
    },
    // 获取安灯设备状态
    async getDeviceState() {
      try {
        const res = await getAndonKanbanOfRiSheng({}, {
          current: 1,
          size: 99,
          key: this.kanbanConfig.key || 'oOg1hjGXEDw='
        });
        // const coatingCode = '00000176_00000021';
        // const coatingData = {
        //   deviceCode: '00000176_00000021',
        //   deviceName: '喷涂1线',
        //   deviceOperator: '',
        //   devicePriCode: '1',
        //   deviceRealState: 1,
        //   groupId: 5,
        //   lightStates: ['red_e', 'blue_m']
        // };
        // const deviceList = new Array(20).fill(0).map((item, index) => ({
        //   deviceCode: '00000176_00000001' + index,
        //   deviceName: `成型机${index}`,
        //   deviceOperator: '',
        //   devicePriCode: '1',
        //   deviceRealState: 1,
        //   groupId: 5,
        //   lightStates: ['red_e', 'blue_m']
        // }));
        // this.coatingData = coatingData;
        // this.deviceList = deviceList;
        this.deviceList = res.rows || [];
        this.deviceCount[0].value = res.total;
        this.redDeviceCodes.forEach(code => {
          this.deviceList.forEach(item => {
            if (code === item.deviceCode) {
              item.overtime = true;
            }
          });
        });
        // console.log(this.deviceList);
        this.deviceCount.forEach(info => {
          if (info.key) {
            let sum = 0;
            this.deviceList.forEach(item => {
              if (item.lightStates.includes(info.key) && item.deviceRealState !== 3) {
                sum++;
              }
            });
            info.value = sum;
          }
        });
      } catch (error) {};
    },
    // 获取异常警告列表
    async getWarnList() {
      try {
        const res = await getAndonExceptionWarnReport({}, {
          key: this.kanbanConfig.key || 'oOg1hjGXEDw='
        });
        // const data = new Array(14).fill(0).map((item, index) => ({
        //   deviceName: `成型机${index + 1}`,
        //   requestTime: '09:10:23',
        //   type: '异常',
        //   responseTime: '09:20:23',
        //   responseDuration: '12',
        //   state: '维护超时10分钟'
        // }));
        // this.warnList = data;
        // this.redDeviceCodes = ['00000176_000000013', '00000176_000000016', '00000176_00000021'];
        this.redDeviceCodes = res.rows.redDeviceCodes;
        // const test = new Array(5).fill(res.rows.exceptionWarnMeta[0]);
        this.warnList = res.rows.exceptionWarnMeta;
        // this.warnList = test;
      } catch (error) {};
    },
    loadData() {
      this.getWarnList();
      this.getDeviceState();
      this.getTodayCallout();
      this.getMonthMaintain();
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ * {
  box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 0px;
} /* 这是针对缺省样式 (必须的) */
::-webkit-scrollbar {
  width: 0px;
} /* 这是针对缺省样式 (必须的) */
@font-face {
  font-family: Vox-Regular2;
  src: url('~@/assets/font/VOX_WIDE_BOLD.woff');
}
@font-face {
  font-family: JLinXin;
  src: url('~@/assets/font/JLinXin.woff');
}
p {
  margin: 0 0 0px;
}
.kanban {
  background: url('~@/assets/img/kanban/background.png') no-repeat;
  background-size: 100% 100%;
  overflow-y: hidden;
  color: #fff;
}
.titleHead {
  height: 8rem;
  width: 100%;
  background-image: url('~@/assets/img/kanban/title4.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.titleHead p {
  font-size: 3.4rem;
  font-family: JLinXin;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  text-shadow: 0.3rem 0.3rem 0.3rem #03a9f4;
  letter-spacing: 0.4rem;
  line-height: 5.2rem;
}
#time {
  position: absolute;
  right: 0.9rem;
  top: -0.6rem;
  letter-spacing: 0px;
  font-size: 2rem;
  margin: 0;
  color: #4bf3f9;
  font-family: Vox-Regular2;
}
.kanbanContent {
  display: flex;
  justify-content: space-between;
  height: calc(100vh - 8rem);
  width: 100%;
  padding: 0.5rem 1rem;

  .device_wrapper {
    display: flex;
    justify-content: space-between;
    width: 50%;
    height: 100%;

    .workshop_status {
      height: 100%;
      padding: 1rem 2rem 1rem 3rem;
    }
    .device_count {
      display: flex;
      justify-content: space-around;
      width: 100%;
      height: 12%;
      background-image: url('~@/assets/img/kanban/andon_count_border.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;

      .item_count {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 14%;

        .name {
          font-size: 2.4rem;
          font-family: Vox-Regular2;
        }
        .value {
          font-size: 3.4rem;
          font-family: Vox-Regular2;
        }
      }
    }
    .device_content {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      // align-content: space-around;
      width: 100%;
      height: 88%;
      padding: 1rem;

      &_item {
        width: 33.33%;
        height: 14.28%;
        padding: 0.5%;
      }
    }
    .green_p {
      // background: #007E2E;
      background-color: #00e62c;
    }

    .yellow_off {
      background-color:#F5CA00;
    }

    .red_e {
      background-color: #FF0700;
    }

    .blue_m {
      // background: #7E7BFF;
      background-color: #07aaff;
    }

    .white_m {
      // background: #FF6B22;
      background-color: #ffffff;
      color: #333333;
    }

    .green_w {
      background-color: #7e7bff;
    }
    .device_state {
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 0.5rem;
      background-image: url('~@/assets/img/kanban/deviceBorder.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;

      .name {
        height: 20%;
        margin-top: 0.5rem;
        margin-bottom: 0.8rem;
        font-size: 1.6rem;
        font-family: Vox-Regular2;
      }
      .state {
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        height: 78%;
        // padding: 0 2rem;
        .outer {
          display: flex;
          // width: 50%;
          justify-content: center;
          align-items: center;
        }
        .overTime {
